import React, {SyntheticEvent} from 'react';
import {GameOptions, PlayerType, WebSpaceDuel} from "./WebSpaceDuel/WebSpaceDuel";
import {Instructions} from "./Instructions";

class GameContainer extends React.Component<{}, GameOptions> {
    private readonly canvasDivRef : React.RefObject<HTMLDivElement>;
    private readonly game: WebSpaceDuel;

    constructor(props: any) {
        super(props);
        this.canvasDivRef = React.createRef();
        this.game = new WebSpaceDuel();

        this.state = {
            player1: PlayerType.Human,
            player2: PlayerType.AILevel1,
            width: 800,
            height: 600,
            showInstructions: false
        };
    }

    componentDidMount() {
        if(this.canvasDivRef && this.canvasDivRef.current) {
            //startGame(this.canvasDivRef.current);
            this.game.mountGame(this.canvasDivRef.current);
            this.handleNewGame();
        }
    }

    handlePauseGame() {
        this.game.pauseGame();
    }

    handleNewGame() {
        this.game.newGame(this.state);
    }

    handleCheckChange(e: SyntheticEvent) {
        let target = e.target as HTMLInputElement;
        let newValue = target.checked ? PlayerType.Human : PlayerType.AILevel1;
        if (target.name === "player1") {
            console.log("setting player 1 to ", newValue);
            this.setState({player1: newValue});
        }

        if (target.name === "player2"){
            console.log("setting player 2 to ", newValue)
            this.setState({player2: newValue});
        }
    }

    toggleInstructions(e: SyntheticEvent) {
        this.setState({showInstructions: !this.state.showInstructions});
    }

    render() {
        return (
            <div className="App-game">

                <button onClick={this.handleNewGame.bind(this)}>New Game</button>
                <button onClick={this.toggleInstructions.bind(this)}>Instructions</button>

                <label htmlFor={"player1"}>Player 2 is human</label>
                <input type={"checkbox"}
                       name={"player2"}
                       id={"player2"}
                       checked={this.state.player2 === PlayerType.Human}
                       onChange={this.handleCheckChange.bind(this)}
                />

                { (this.state.showInstructions) ? Instructions(this.state) : ""}
                {/*<button onClick={this.handlePauseGame.bind(this)}>{this.game.isPaused() ? "Resume Game" : "Pause Game"}</button> */}
                <div className={"playwindow"} ref={this.canvasDivRef} />

            </div>
        );
    }

    unused() {
        return (
            <div>
            <label htmlFor={"player1"}>Player 1 is human</label>
                <input type={"checkbox"}
                       name={"player1"}
                       id={"player1"}
                       checked={this.state.player1 === PlayerType.Human}
                       onChange={this.handleCheckChange.bind(this)}
                />


            </div>
        )
    }
}

export default GameContainer;