import Matter, {Body, Composite, Engine, Events, ICollision, Render, Runner} from "matter-js";
import explosionSprite from './explos09.png';
import ship1Sprite from './ship1.png';
import ship1SpriteThrust from './ship1-thrust.png';
import ship2Sprite from './ship2.png';
import ship2SpriteThurst from './ship2-thrust.png';
import sunSprite from './sun.png';
import backgroundSprite from './backgr.png';
import {Simulate} from "react-dom/test-utils";

const SUN_INFO = {
    radius: 36
}

const PLAYER_INFO = {
    height: 34,
    width: 21,
    startingVelocity: 3,
    startingDistance: 200
}

const GRAVITY_CONSTANT = -0.0001;

export class GameRunner {
    private tickInput: {player1: PlayerInput, player2: PlayerInput};
    private _running: boolean;
    private readonly player1IsHuman: boolean;
    private readonly player2IsHuman: boolean;
    private player1IsAlive: boolean;
    private player2IsAlive: boolean;


    // Matter-JS
    private readonly engine: Engine;
    private readonly render: Render;
    private readonly runner: Runner;

    // bodies
    private readonly dimensions: Matter.Vector;
    private readonly center: Matter.Vector;
    private readonly sun : Matter.Body;
    private readonly player1: Matter.Body;
    private readonly player2: Matter.Body;

    constructor(mountElement: HTMLDivElement,
                player1IsHuman: boolean,
                player2IsHuman: boolean,
                width: number,
                height: number
    ) {
        this.player1IsHuman = player1IsHuman;
        this.player2IsHuman = player2IsHuman;
        this.dimensions = Matter.Vector.create(width, height);
        this.center = Matter.Vector.create(Math.floor(width/2), Math.floor(height/2));

        // MatterJS Engine
        this.engine = Engine.create();
        this.engine.gravity.scale = 0;

        mountElement.setAttribute("style",
            `background-image: url('${backgroundSprite}'); background-repeat: repeat`
        );

        this.render = Render.create({
            element: mountElement,
            engine: this.engine,
            options: {
                wireframes: false,
                background: "transparent",
                width: width,
                height: height
            }
        });
        Render.run(this.render);
        this.runner = Runner.create();
        Runner.run(this.runner, this.engine);
        this._running = true;

        // Game Tick (need to wrap so 'this' works)
        Events.on(this.runner, "tick", this.handleTick.bind(this) );
        Events.on(this.engine, "collisionStart", this.handleCollision.bind(this) );

        // Add sun and Two player elements
        this.sun = this.buildSun();
        this.player1 = this.buildPlayer(0.5, ship1Sprite);
        this.player1IsAlive = true;
        this.player2 = this.buildPlayer(1.5, ship2Sprite);
        this.player2IsAlive = true;
        Composite.add(this.engine.world, [this.sun, this.player1, this.player2]);

        // Keyboard events
        this.tickInput = {
            player1: {
                rotateLeft: false,
                rotateRight: false,
                accelUp: false,
                accelDown: false,
                fireBullet: false
            },
            player2: {
                rotateLeft: false,
                rotateRight: false,
                accelUp: false,
                accelDown: false,
                fireBullet: false
            }
        }

        document.addEventListener("keydown", this.handleKeyDown.bind(this) );
        document.addEventListener("keyup", this.handleKeyUp.bind(this) );
    }
    public isRunning(): boolean {
        return this._running;
    }
    public setRunning(running: boolean) {
        this._running = running;
        this.runner.enabled = running;
    }
    public destroy() {
        Render.stop(this.render);
        this.render.element.innerHTML = "";
        Runner.stop(this.runner);
        Composite.rebase(this.engine.world);
        Engine.clear(this.engine);
    }

    private handleCollision(event: Matter.IEventCollision<Body>): void {
        for (let pair of event.pairs) {
            let a = pair.bodyA;
            let b = pair.bodyB;
            for (let body of [a, b]){

                if (body === this.player1) {
                    // player 1 dies
                    this.createExplosionAt(body.position);
                    this.player1IsAlive = false;
                }

                if (body === this.player2) {
                    // player 2 dies
                    this.createExplosionAt(body.position);
                    this.player2IsAlive = false;
                }

                if (body === this.sun){
                    // sun outlives us all
                } else {
                    Composite.remove(this.engine.world, body);
                }
            }
        }
    }

    private handleTick(event: Matter.IEvent<Body>): void {

        let allBodies = Composite.allBodies(this.engine.world);
        for(let body of allBodies) {
            if (body !== this.sun) {
                this.applyGravity(this.sun, body);
            }
        }

        if(this.player1IsHuman && this.player1IsAlive){
            if (this.tickInput.player1.accelUp) {
                this.accelBody(this.player1);
                if (this.player1.render.sprite) {
                    this.player1.render.sprite.texture = ship1SpriteThrust;
                }
            }
            else {
                if (this.player1.render.sprite) {
                    this.player1.render.sprite.texture = ship1Sprite;
                }
            }
            if (this.tickInput.player1.rotateRight){
                this.rotateBody(this.player1, 1);
            }
            if (this.tickInput.player1.rotateLeft){
                this.rotateBody(this.player1, -1);
            }
            if (this.tickInput.player1.fireBullet){
                this.shootBullet(this.player1, "1",'#FF0000');
                this.tickInput.player1.fireBullet = false;
            }
        }

        let player2Input = (!this.player2IsHuman && this.player2IsAlive) ?
            this.getAIInput()
        :
            this.tickInput.player2
        ;


        if(this.player2IsAlive){
            if (player2Input.accelUp) {
                this.accelBody(this.player2);
                if (this.player2.render.sprite) {
                    this.player2.render.sprite.texture = ship2SpriteThurst;
                }
            }
            else {
                if (this.player2.render.sprite) {
                    this.player2.render.sprite.texture = ship2Sprite;
                }
            }
            if (player2Input.rotateRight){
                this.rotateBody(this.player2, 1);
            }
            if (player2Input.rotateLeft){
                this.rotateBody(this.player2, -1);
            }
            if (player2Input.fireBullet){
                this.shootBullet(this.player2, "2", '#0000FF');
                this.tickInput.player2.fireBullet = false;
            }
        }
    }

    private getAIInput(): PlayerInput {
        let playerInput: PlayerInput = {
            rotateLeft: false,
            rotateRight: false,
            accelUp: false,
            accelDown: false,
            fireBullet: false
        };

        let doSomething = Math.random();
        if(doSomething > 0.2) {
            return playerInput;
        }

        let input = Math.random();

        if (input >= 0.0 && input < 0.1) {
            playerInput.accelUp = true;
        }
        if (input >= 0.1 && input < 0.4){
            playerInput.rotateLeft = true;
        }
        /*
        if (input >= 0.4 && input < 0.6){
            playerInput.rotateRight = true;
        }
        */
        if (input >= 0.95){
            playerInput.fireBullet = true;
        }

        return playerInput;
    }

    private applyGravity(fromSun: Matter.Body, toBody: Matter.Body) {
        let gravityVector = Matter.Vector.create(
            toBody.position.x - fromSun.position.x,
            toBody.position.y - fromSun.position.y
        );

        Matter.Body.applyForce(
            toBody,
            fromSun.position,
            Matter.Vector.mult(Matter.Vector.normalise(gravityVector), GRAVITY_CONSTANT)
        );
    }

    private rotateBody(body: Matter.Body, direction: 1 | -1){
        Matter.Body.rotate(body, direction * 0.2);
    }

    private accelBody(body: Matter.Body){
        let upVector = Matter.Vector.rotate(Matter.Vector.create(0.0001, 0), body.angle);
        Matter.Body.applyForce(body, body.position , upVector);
    }

    private shootBullet(body: Matter.Body, label: string, color: string) {
        let startingDirection = Matter.Vector.rotate(Matter.Vector.create(15,0), body.angle);
        let startingPosition = Matter.Vector.add(body.position, startingDirection);
        let bullet  = Matter.Bodies.circle(startingPosition.x, startingPosition.y, 2);
        bullet.label = label;
        bullet.frictionAir = 0;
        bullet.frictionStatic = 0;
        bullet.render.fillStyle = color;

        let shootVector = Matter.Vector.rotate(Matter.Vector.create(5, 0), body.angle);

        Matter.Body.setVelocity(bullet, shootVector);
        Matter.Body.setMass(bullet, bullet.mass * 10);

        Composite.add(this.engine.world, bullet);
    }

    private addSprite(body: Matter.Body, sprite: string) {
        if(body.render.sprite){
            body.render.sprite.texture = sprite;
        } else {
            body.render.sprite = {
                texture: sprite,
                xScale: 1,
                yScale: 1
            } as Matter.IBodyRenderOptionsSprite;
        }
    }


    private createExplosionAt(position: Matter.Vector) {
        let boom  = Matter.Bodies.circle(position.x, position.y, 36, {isStatic: true});
        boom.collisionFilter.mask = 8;
        boom.collisionFilter.group = 2;
        boom.collisionFilter.category = 2;
        this.addSprite(boom, explosionSprite);

        Composite.add(this.engine.world, boom);
    }

    private buildSun(): Matter.Body {
        let sun = Matter.Bodies.circle(this.center.x, this.center.y, SUN_INFO.radius, {isStatic: true});
        sun.render.strokeStyle = '#000000';
        this.addSprite(sun, sunSprite);
        return sun;
    }

    private buildPlayer(radianStartingSpot: number, sprite: string): Matter.Body {

        let position = Matter.Vector.add(
            Matter.Vector.create(
                PLAYER_INFO.startingDistance * Math.cos(radianStartingSpot * Math.PI),
                PLAYER_INFO.startingDistance * Math.sin(radianStartingSpot * Math.PI)
            ),
            this.center
        );

        let player = Matter.Bodies.rectangle(position.x, position.y, PLAYER_INFO.width, PLAYER_INFO.height);
        player.frictionAir = 0;
        player.frictionStatic = 0;
        player.render.fillStyle = '#ff0000';
        this.addSprite(player, sprite);
        let perpendicularVelocityAngle = ( Matter.Vector.angle(this.center, player.position) + 90 ) % 360;
        Matter.Body.setVelocity(player,
            Matter.Vector.rotate(Matter.Vector.create(-PLAYER_INFO.startingVelocity, 0), perpendicularVelocityAngle)
        );
        return player;
    }

    private handleKeyDown(event: KeyboardEvent): void {
        if(this.player1IsHuman) {
            this.tickInput.player1[player1KeyMapping[event.key]] = true;
        }
        if(this.player2IsHuman) {
            this.tickInput.player2[player2KeyMapping[event.key]] = true;
        }
    }

    private handleKeyUp(event: KeyboardEvent): void {
        if(this.player1IsHuman) {
            this.tickInput.player1[player1KeyMapping[event.key]] = false;
        }
        if(this.player2IsHuman) {
            this.tickInput.player2[player2KeyMapping[event.key]] = false;
        }
    }
}

const player1KeyMapping: {[key: string] : "rotateLeft" | "accelDown" | "rotateRight" | "accelUp" | "fireBullet" } = {
    "a": "rotateLeft",
    "s": "accelDown",
    "d": "rotateRight",
    "w": "accelUp",
    "q": "fireBullet"
}

const player2KeyMapping: {[key: string] : "rotateLeft" | "accelDown" | "rotateRight" | "accelUp" | "fireBullet" } = {
    "j": "rotateLeft",
    "k": "accelDown",
    "l": "rotateRight",
    "i": "accelUp",
    "u": "fireBullet"
}

interface PlayerInput {
    rotateLeft: boolean,
    rotateRight: boolean,
    accelUp: boolean,
    accelDown: boolean,
    fireBullet: boolean
}